body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  height: 100vh;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

header{
    height: 94px;
    position: fixed;
    width: 100%;
    background: #00B4DB;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to left, #0083B0, #00B4DB); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    box-shadow: 0 10px 10px #7f7f7f59;
    display: flex;
}

.logo {
    background: url(/projetoSaude/static/media/logo.61d1db48.png);
    height: 80px;
    width: 80px;
    background-size: 100px;
    background-position: -10px;
    margin: 6px 20px 0 100px;
    border-radius: 80px;
}

h1 {
    color: white;
}
.TableauDashboard {
    display: flex;
    flex-direction: column;
    padding: 20px;
    position: relative;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: auto;
    background: white;
    border-radius: 10px 10px 20px 20px;
    box-shadow: 5px 5px 10px 0 #9abae3;
}

.TableauDashboard div:last-child {
    justify-content: flex-end;
}



.TableauDashboard > div:first-child {
    margin-top: 20px;
}
.viewHolder {
    width: 25%;
    background: white;
    border-radius: 10px;
    overflow: hidden;
    margin-bottom: 60px;
    box-shadow: 5px 5px 25px #e1e1e1;
}

.viewHolder h3 {
    text-align: center;
    color: #aeaeae;
}

.viewHolder.full {
    width: 100%;
}

.viewHolder iframe {
    width: 100% !important;
}

.viewHolder.fixed-height iframe {
    height: 500px !important;
}

.viewHolder.extra-height iframe {
    height: 620px !important;
}

.viewHolder.half {
    width: 47%;
}

.viewHolder.hide {
    display: none;
}
.home{
    display: flex;
    flex-wrap: wrap;
    padding:  0 100px;
    justify-content: space-between;
}

.filter {
    position: fixed;
    right: 50px;
    top: 20px;
    border-radius: 50px;
    color: white;
    padding: 15px;
    font-size: 18px;
    border: 1.5px solid white;
    cursor: pointer;
    transition: all ease-in .1s;
    z-index: 2;
    width: 157px;
    text-align: center;
}

.filter:hover {
    -webkit-transform: scale(1.02);
            transform: scale(1.02);
}
section {
    padding-top: 150px;
    max-height: 81%;
    overflow-y: scroll;
}
